/* Internal Data */
	var Support = false;

/* Handle Error Event */
	function OnError_NotSupport() {
		console.error("%cJavaScript Macro - Browser - Storage", "font-weight: bold;", "\n", "Storage is not enabled/supported in this browser!");
	};
	function OnError_Key(Method, Key) {
		console.error("%cJavaScript Macro - Browser - Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
	};
	function OnError_KeyArray(Method, Key) {
		console.error("%cJavaScript Macro - Browser - Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
	};

/* Determine Support Status */
	if (localStorage) {
		Support = true;
	};
	function Status() {
		return Support;
	};

/* Set */
	function Set(Key, Value = null) {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		if (typeof Key == "string") {
			try {
				localStorage.setItem(Key, Value);
			} catch (Error) {
				console.warn("[JavaScript Macro - Browser - Storage] " + Error);
			};
		} else {
			OnError_Key("Set", Key);
		};
	};

/* Get */
	function Get_Internal(Key) {
		return localStorage.getItem(Key);
	}
	function Get(Key = null) {
		if (Support != true) {
			OnError_NotSupport();
			return undefined;
		};
		if (Key == null) {
			let StoragePair = {};
			let StoragePairCount = localStorage.length;
			if (StoragePairCount > 0) {
				for (let index = 0; index < StoragePairCount; index++) {
					let StoragePairKey = localStorage.key(index);
					StoragePair[StoragePairKey] = Get_Internal(StoragePairKey);
				};
			};
			return StoragePair;
		} else if (typeof Key == "string") {
			return Get_Internal(Key);
		} else if (Array.isArray(Key) == true) {
			let ValueArray = [];
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					ValueArray.push(Get_Internal(Key[index]));
				} else {
					OnError_KeyArray("Get", Key[index]);
					ValueArray.push(undefined);
				};
			};
			return ValueArray;
		} else {
			OnError_Key("Get", Key);
			return undefined;
		};
	};

/* Delete */
	function Delete_Internal(Key) {
		localStorage.removeItem(Key);
	};
	function Delete(Key) {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		if (typeof Key == "string") {
			Delete_Internal(Key);
		} else if (Array.isArray(Key) == true) {
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					Delete_Internal(Key[index]);
				} else {
					OnError_KeyArray("Delete", Key[index]);
				};
			};
		} else {
			OnError_Key("Delete", Key);
		};
	};
	function Del(Key) {
		return Delete(Key);
	};
	function DeleteAll() {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		localStorage.clear();
	};
	function DelAll() {
		return DeleteAll();
	};

/* Package Module */
	export {
		Status,
		Set,
		Get,
		Delete,
		Del,
		DeleteAll,
		DelAll
	};
