/* Internal Data */
	const Support = navigator.cookieEnabled;
	const CookieConfiguration = {
		"ExpireDuration": 365.25 * 24 * 60 * 60,
		"Domain": location.hostname,
		"Secure": false
	};

/* Handle Error Event */
	function OnError_NotSupport() {
		console.error("%cJavaScript Macro - Browser - Cookie", "font-weight: bold;", "\n", "Cookie is not enabled/supported in this browser!");
	};
	function OnError_Key(Method, Key) {
		console.error("%cJavaScript Macro - Browser - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
	};
	function OnError_KeyArray(Method, Key) {
		console.error("%cJavaScript Macro - Browser - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
	};

/* Determine Secure */
	if (location.protocol == "https:") {
		CookieConfiguration["Secure"] = true;
	};

/* Support Status */
	function Status() {
		return Support;
	};

/* Expire Duration */
	function ExpireDuration(Second = null) {
		if (Second == null) {
			return CookieConfiguration["ExpireDuration"];
		} else if (Number.isSafeInteger(Second) && Second > 0) {
			CookieConfiguration["ExpireDuration"] = Second;
		} else {
			console.warn("[JavaScript Macro - Browser - Cookie] Invalid expire duration \"" + JSON.stringify(Second) + "\"! Expire duration must be type of number, safe integer, and larger than 0!");
		};
	};

/* Internal Function */
	function Set_Internal(Key, Value, Decay = false) {
		let ExpireTime;
		if (Decay == true) {
			let OldTime = new Date(0);
			ExpireTime = OldTime.toUTCString();
		} else {
			let CurrentTime = new Date();
			CurrentTime.setTime(CurrentTime.getTime() + (CookieConfiguration["ExpireDuration"] * 1000));
			ExpireTime = CurrentTime.toUTCString();
		};
		let FullCookie = encodeURIComponent(Key) + "=" + encodeURIComponent(Value) + "; expires=" + ExpireTime + "; domain=" + CookieConfiguration["Domain"] + "; path=/";
		if (CookieConfiguration["Secure"] == true) {
			FullCookie += "; secure";
		};
		document.cookie = FullCookie;
	};

/* Set */
	function Set(Key, Value = null) {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		if (typeof Key == "string" && (typeof Value == "string" || typeof Value == "number" || typeof Value == "boolean")) {
			Set_Internal(Key, Value, false);
		} else {
			if (typeof Key != "string") {
				OnError_Key("Set", Key);
			};
			if (typeof Value != "string" && typeof Value != "number" && typeof Value != "boolean") {
				console.warn("[JavaScript Macro - Browser - Cookie] Invalid value \"" + JSON.stringify(Value) + "\" in \"Set\" request! Value must be type of string/number/boolean!");
			};
			return;
		};
	};

/*Get*/
	function Get(Key = null) {
		if (Support != true) {
			OnError_NotSupport();
			return undefined;
		};
		let CookieList = document.cookie;
		CookieList = CookieList.split(";");
		let CookiePair = {};
		if (CookieList.length > 0) {
			for (let index = 0; index < CookieList.length; index++) {
				let CookiePiece = CookieList[index].split("=");
				while (CookiePiece[0].charAt(0) == " ") {
					CookiePiece[0] = CookiePiece[0].substring(1);
				};
				CookiePair[decodeURIComponent(CookiePiece[0])] = decodeURIComponent(CookiePiece[1]);
			};
		};
		if (Key == null) {
			return CookiePair;
		} else if (typeof Key == "string") {
			return CookiePair[Key];
		} else if (Array.isArray(Key) == true) {
			let ValueArray = [];
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					ValueArray.push(CookiePair[Key[index]]);
				} else {
					OnError_KeyArray("Get", Key[index]);
					ValueArray.push(undefined);
				};
			};
			return ValueArray;
		} else {
			OnError_Key("Get", Key);
			return undefined;
		};
	};

/*Delete*/
	function Delete(Key) {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		if (typeof Key == "string") {
			Set_Internal(Key, 0, true);
		} else if (Array.isArray(Key) == true) {
			for (let index = 0; index < Key.length; index++) {
				if (typeof Key[index] == "string") {
					Set_Internal(Key[index], 0, true);
				} else {
					OnError_KeyArray("Delete", Key[index]);
				};
			};
		} else {
			OnError_Key("Delete", Key);
		};
	};
	function Del(Key) {
		return Delete(Key);
	};
	function DeleteAll() {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		let KeyArray = Object.keys(Get());
		for (let index = 0; index < KeyArray.length; index++) {
			Set_Internal(KeyArray[index], 0, true);
		};
	};
	function DelAll() {
		return DeleteAll();
	};

/* Rebirth */
	function Rebirth() {
		if (Support != true) {
			OnError_NotSupport();
			return;
		};
		let CookiePair = Get();
		let KeyArray = Object.keys(CookiePair);
		let ValueArray = Object.values(CookiePair);
		for (let index = 0; index < KeyArray.length; index++) {
			Set(KeyArray[index], ValueArray[index]);
		};
	};

/* Package Module */
	export {
		Status,
		ExpireDuration,
		Set,
		Get,
		Delete,
		Del,
		DeleteAll,
		DelAll,
		Rebirth
	};
