/* Addition */
	Math.addition = function() {
		let Chain = [];
		if (Array.isArray(arguments[0]) == true) {
			Chain = arguments[0];
		} else {
			for (let index = 0; index < arguments.length; index++) {
				Chain.push(arguments[index]);
			};
		};
		if (Chain.length >= 1) {
			for (let index = 0; index < Chain.length; index++) {
				if (typeof Chain[index] != "number") {
					console.error("Math.addition: \"" + JSON.stringify(Chain[index]) + "\" is not a number!");
					return undefined;
				};
			};
		} else {
			console.error("Math.addition: No input!");
			return undefined;
		};
		let Total = Chain[0];
		if (Chain.length > 1) {
			for (let index = 1; index < Chain.length; index++) {
				Total = Total + Chain[index];
			};
		};
		return Total;
	};
	Math.add = Math.addition;

